<template>
    <div id="login">
        <h4></h4>
        <el-input v-model="username" placeholder=""></el-input>
        <h4></h4>
        <el-input v-model="password" placeholder="" @keyup.enter.native="onRegister"></el-input>
        <el-button type="primary" plain @click="onRegister"></el-button>
        <p class="notice">
            <router-link to="/pages/login"></router-link>
        </p>
    </div>
</template>

<script>
    import {mapActions} from 'vuex'
    import request from '../../helpers/request.js'
    import auth from '../../api/auth.js'


    window.request = request;
    window.auth = auth;
    export default {
        name: "register",
        data() {
            return {
                username: '',
                password: ""
            }
        },
        methods: {
            ...mapActions(['register']),
            onRegister() {
                this.register({username: this.username, password: this.password}).then(() => {
                    this.$router.push({path: "/pages/index"})
                }).catch(() => {
                })
            }

        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../assets/commonStyle/base.stylus"

    #login > *, #register > *
        margin 5px 0

    #login, #register
        display grid
        justify-content center
        h4
            font-weight 500
        .el-input
            width 100%
            margin 0 auto
        p.error
            color $themeWarningTextColor
            font-size 12px
        .notice
            font-size 12px
</style>