<template>
    <div id="edit">
        <h1></h1>
        <h3></h3>
        <el-input v-model="articleTitle" placeholder=""></el-input>
        <p class="msg">{{lengthOfTitle}}/30</p>
        <h3></h3>
        <el-input
                type="textarea"
                :autosize="{ minRows: 2, maxRows: 4}"
                placeholder=""
                v-model="articleSummary">
        </el-input>
        <p class="msg">{{lengthOfSummary}}/30</p>
        <h3></h3>
        <el-input
                type="textarea"
                :autosize="{ minRows: 6, maxRows: 30}"
                placeholder=""
                v-model="articleContent">
        </el-input>
        <p class="at-index-choice">
            <label></label>
            <el-switch
                    v-model="atIndex"
                    active-color="#13ce66"
                    inactive-color="#ff4949">
            </el-switch>
        </p>
        <el-button type="primary" plain @click="onCreate"></el-button>
    </div>
</template>

<script>
    import {Message} from 'element-ui'
    import blog from "../../api/blog"

    export default {
        name: "create",
        data() {
            return {
                articleTitle: '',
                articleSummary: '',
                articleContent: '',
                atIndex: false
            }
        },
        computed: {
            lengthOfTitle() {
                let that = this
                if (that.articleTitle.length > 30) {
                    Message.error("")
                }
                return this.articleTitle.length
            },
            lengthOfSummary() {
                let that = this
                if (that.articleSummary.length > 30) {
                    Message.error("")
                }
                return this.articleSummary.length
            }
        },
        methods: {
            onCreate() {
                blog.createBlog({
                    title: this.articleTitle,
                    content: this.articleContent,
                    description: this.articleSummary,
                    atIndex: this.atIndex
                }).then(res => {
                    this.$message.success(res.msg)
                    this.$router.push({path: `{/pages/blog-details/${res.data.id}`})
                })
            }
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../assets/commonStyle/base.stylus"

    #edit > *
        margin 5px 0

    #edit
        display grid
        justify-content center
        h1
            text-align: center
            font-weight 500
        .el-input
            width 600px
        p.msg
            color $themeWarningTextColor
        p.at-index-choice
            margin 15px 0
</style>